/* rhodium-libre-regular - latin */
@font-face {
  font-family: 'Rhodium Libre';
  font-style: normal;
  font-weight: 400;
  src: local("Rhodium Libre"), local("RhodiumLibre-Regular"), url("/static/assets/fonts/rhodium-libre-v2-latin-regular.woff2") format("woff2"), url("/static/assets/fonts/rhodium-libre-v2-latin-regular.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Main Styles */
html, body {
  height: 100%;
  font-family: "Rhodium Libre", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  line-height: 1.8;
}
@media (min-width: 781px) {
html, body {
      font-size: 18px;
}
}
body {
  padding: 0;
  position: relative;
  color: #222020;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 0;
}
p {
  margin-top: 0;
  margin-bottom: 0;
}
p + p, h2 + p {
    margin-top: 25px;
}
h1, .size-1 {
  font-size: 3.5rem;
}
h2, .size-2 {
  font-size: 2rem;
}
@media (min-width: 781px) {
h3, .size-3 {
    font-size: 1rem;
}
}
@media (min-width: 781px) {
.size-4 {
    font-size: 0.78rem;
}
}
@media (min-width: 781px) {
.size-5 {
    font-size: 0.67rem;
}
}
a {
  color: #222020;
  display: inline-block;
  text-decoration: none;
  transition: all 0.2s ease-out;
}
a:hover {
    transform: translateY(-5%);
}
#app {
  text-align: center;
}



